@font-face {
    font-family: 'ProximaNova';
    font-style: normal;
    font-weight: 400;
    src: url(/static/media/ProximaNovaRegular.410504d4.otf) format('opentype');
  }
  @font-face {
    font-family: 'ProximaNova';
    font-style: normal;
    font-weight: 600;
    src: url(/static/media/ProximaNovaSemibold.6a386899.otf) format('opentype');
  }
  @font-face {
    font-family: 'ProximaNova';
    font-style: normal;
    font-weight: 800;
    src: url(/static/media/ProximaNovaBold.72b6f2c9.otf) format('opentype');
  }

  html *{
      font-family: 'ProximaNova' !important;
  }

  #root, #grid{
      height: 100vh;
      padding: 20px;
  }

  .grey--text{
      color:#737d87;
  }

  .ml-2{
      margin-left: 10px;
  }

  .mr-2{
    margin-right: 10px;
  }

  .header{
    font-size: 60px;
    margin:0;
  }

  .subheader{
      margin:0;
      font-weight: 400;
      margin-bottom:20px;
  }

  .post{
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    -webkit-flex-basis: calc(100%);
            flex-basis: calc(100%);
    height:auto;
    border:solid 1px #ddd;
    margin: 5px;
    max-width: none;
    min-width: 0;
    position: relative;
  }

  .post:hover .post-overlay, .post:active .post-overlay{
    visibility: hidden;
    visibility: visible;
    opacity: 1;
  }

  .post:before {
    content:'';
    float:left;
    padding-top:100%;
  }

  .post .post-overlay *{
    color:#FFF;
  }

  .post .post-avatar{
    border: solid 2px white;
  }

  .post .post-overlay{
      position: absolute;
      top: 0;
      padding:0;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,.6);
      display:-webkit-flex;
      display:flex;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items:center;
              align-items:center;
      -webkit-flex-direction: column;
              flex-direction: column;
      opacity: 0;
      transition: visibility 0s, opacity 0.2s linear;
  }

  .post .post-image{
    height:100%;
    background-position: center;
  }

  .post .post-user{
    position: absolute;
    top: 0;
    width: 100%;
    left: 0;
  }

  button.button{
    text-transform: capitalize;
    margin-top: 15px;
    font-size: 18px;
    color:white;
    background:#737d87;
  }

  .button.black{
    background: black;
    margin: 0;
    margin-left: 10px;
    margin-top: 5px;
  }

  button.button.outlined{
    background:transparent;
    border-color:white;
    border-radius: 30px;
  }

  .button.button.outlined:hover{
    background:rgba(0,0,0,.3);
  }

  button.button:hover{
    background:#8b949e;
    color:white;
  }


  @media only screen and (min-width: 768px) {
    .post{
      -webkit-flex-grow: 0;
              flex-grow: 0;
      -webkit-flex-shrink: 0;
              flex-shrink: 0;
      -webkit-flex-basis: calc(50%);
              flex-basis: calc(50%);
    }
  } 
  @media only screen and (min-width: 992px) {
    .post{
      -webkit-flex-grow: 0;
              flex-grow: 0;
      -webkit-flex-shrink: 0;
              flex-shrink: 0;
      -webkit-flex-basis: calc(20%);
              flex-basis: calc(20%);
    }
  } 
  @media only screen and (min-width: 1400px) {
    .post{
      -webkit-flex-grow: 0;
              flex-grow: 0;
      -webkit-flex-shrink: 0;
              flex-shrink: 0;
      -webkit-flex-basis: calc(16%);
              flex-basis: calc(16%);
    }
  }
